import * as React from "react"
import { Link, navigate } from "gatsby"
import { BannerModuleStyles } from "./BannerModuleStyles"
import { Grid } from "@mui/material"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import Button from "../Button/Button"

export const ArtworkBanner = ({ artwork }) => {
    const {
        title, minimumPrice, introduction, year, picture, color, medium, height, width,
    } = artwork

    // noinspection JSUnusedLocalSymbols
    function scrollToArea() {
        // noinspection JSIgnoredPromiseFromCall
        navigate("#topContent")
    }

    const image = getImage(artwork.gatsbyImage)

    return (<>
        <BannerModuleStyles>
            <GatsbyImage
                className="banner__image"
                imgClassName="banner__image--content"
                image={image}
                alt={title + " - Banner"}
            />

            <div className="container">

                <div className="banner__content">
                    <Grid container spacing={8}>
                        <Grid item xl={6} lg={6} md={6} sm={12} xs={12}>
                            <div className={"artwork-title"}>
                                <h1>
                                    {title}
                                    <span style={{ color: "var(--primary)" }}>.</span>
                                </h1>
                            </div>
                            <div className={"artwork-type"}>
                                <h2>
                                    {color.map((c, i) => {
                                        return c.name + (i < color.length - 1 && " and " || "")
                                    })} on {medium.name}
                                    , {year}
                                </h2>
                            </div>
                            <div className={"artwork-size artwork-details"}>{height} x {width} cm /&nbsp;
                                {Math.round(height / 2.54)} x {Math.round(width / 2.54)} in
                            </div>
                            <div className={"artwork-classification artwork-details"}>This is a unique work.</div>
                            <div className={"artwork-price"}>
                                <div className="banner__btns">
                                    <h3>
                                        {minimumPrice} â‚¬
                                        <span style={{ color: "var(--primary)" }}>.</span>
                                    </h3>
                                    <Button
                                        className="btn"
                                        text="Enquire Now"
                                        as={Link}
                                        to="/contact"
                                    />
                                </div>
                            </div>
                            <div className={"artwork-description artwork-details"}>
                                This work includes a certificate of authenticity.
                            </div>
                        </Grid>
                        <Grid item xl={6} lg={6} md={6} sm={12} xs={12}>
                            <img src={picture.to_framed_path()} alt={title} width={"80%"}
                                 style={{ maxWidth: "467px", marginLeft: "10%" }} />
                        </Grid>
                    </Grid>

                </div>
            </div>
            <div className="gradient"></div>
        </BannerModuleStyles>
        <span id="topContent"></span>
    </>)
}
